import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, Ocorrencia } from '../../../../../models/dtos/ger/ocorrencia';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const OcorrenciaSearchView: FC<SearchViewProps<Ocorrencia>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'tipo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Tipo',
    },
    {
      dataField: 'subtipo',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Subtipo',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];
  return (
    <WindowComponent title="Gia - Ocorrencia" wikiId={39}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'OcorrenciaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default OcorrenciaSearchView;
